.cms-about .notice--main {
  display: none!important;
}
.cms-about .page-main > .container {
  width: auto!important;
  padding: 0!important;
}
.about-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  color: #fff;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  padding-left: 10px;
  padding-right: 10px;
}
@media (min-width: 768px) {
  .about-section {
    font-size: 16px;
    line-height: 18px;
  }
}
.about-section .btn-about {
  position: absolute;
  left: calc(50% - 14px);
  bottom: 30px;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  color: #32414A;
  font-size: 24px;
  line-height: 30px;
  font-weight: bold;
  flex-shrink: 0;
  padding: 0;
  background-color: #fff;
}
.about-section .btn-about--anim {
  animation: shadow-pulse 2s infinite;
}
.about-h1 {
  width: 100%;
  font-size: 34px;
