  letter-spacing: 0.05em;
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 5px;
}
@media (min-width: 768px) {
  .about-h1 {
    font-size: 60px;
    line-height: 70px;
    margin-bottom: 10px;
  }
}
.about-title {
  width: 100%;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
  margin-bottom: 60px;
}
@media (min-width: 768px) {
  .about-title {
    font-size: 18px;
    line-height: 21px;
