  line-height: 30px;
  width: 30%;
  flex-shrink: 0;
  margin-right: 20px;
}
@media (min-width: 768px) {
  .about-num {
    font-size: 40px;
    line-height: 60px;
    width: auto;
    margin-right: 0;
